.glass-surface {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    transition: opacity 0.26s ease-out;
}

.glass-surface__filter {
    width: 100%;
    height: 100%;
    pointer-events: none;
    position: absolute;
    inset: 0;
    opacity: 0;
    z-index: -1;
}

.glass-surface__content {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
    border-radius: inherit;
    position: relative;
    z-index: 1;
}

.glass-surface--svg {
    background: hsl(0 0% 0% / var(--glass-frost, 0));
    backdrop-filter: var(--filter-id, url(#glass-filter)) saturate(var(--glass-saturation, 1));
    box-shadow:
        0 0 3px 2px color-mix(in oklch, white, transparent 50%) inset,
        0 0 15px 6px color-mix(in oklch, white, transparent 75%) inset,
        0px 4px 16px rgba(17, 17, 26, 0.05),
        0px 8px 24px rgba(17, 17, 26, 0.05),
        0px 16px 56px rgba(17, 17, 26, 0.05),
        0px 4px 16px rgba(17, 17, 26, 0.05) inset,
        0px 8px 24px rgba(17, 17, 26, 0.05) inset,
        0px 16px 56px rgba(17, 17, 26, 0.05) inset;
}

.glass-surface--fallback {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(25px) saturate(1.8) brightness(1.2);
    -webkit-backdrop-filter: blur(25px) saturate(1.8) brightness(1.2);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow:
        inset 0 1px 0 0 rgba(255, 255, 255, 0.4),
        inset 0 -1px 0 0 rgba(255, 255, 255, 0.15);
}

@supports not (backdrop-filter: blur(10px)) {
    .glass-surface--fallback {
        background: rgba(0, 0, 0, 0.4);
    }

    .glass-surface--fallback::before {
        content: '';
        position: absolute;
        inset: 0;
        background: rgba(255, 255, 255, 0.05);
        border-radius: inherit;
        z-index: -1;
    }
}

.glass-surface:focus-visible {
    outline: 2px solid #0a84ff;
    outline-offset: 2px;
}